import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import * as Actions from '../actions/bet';
import * as helpers from '../utils/helpers';
import x from '../icons/icon_x.svg';
import store from '../stores';

const mapStateToProps = (state) => ({
    multiplier: state.multiplier,
    world: state.user,
});

const multiplier = store.getState().multiplier;


class BetboxMultiplier extends Component {
    componentWillMount(){
        this.unsubscribe = store.subscribe((onChange) =>{
            this.forceUpdate();
        });
    };
    componentWillUnmount(){
        this.unsubscribe();
    }
    onMultiplierChange(e) {
        let str = e.target.value;
        store.dispatch(Actions.updateMultiplier({str: str}));
    }

    minMultiplier(newStr){
        newStr = 1.01;
        store.dispatch(Actions.updateMultiplier({str: newStr.toString()}));
    }
    halveMultiplier(n) {
        n = Math.round(multiplier.num / 2);
        store.dispatch(Actions.updateMultiplier({str: n.toString()}));
    }
    doubleMultiplier(str){
        str = Math.round(multiplier.num * 2);
        store.dispatch(Actions.updateMultiplier({str: str.toString()}));
    }
    maxMultiplier(newMult){
        newMult = 44000;
        store.dispatch(Actions.updateMultiplier({str: newMult.toString()}));
    }
    render () {
        return (
        <section className="betbox_multiplier">
            <span id="label"> MULTIPLIER </span>
            <div className="input_group">
                <div className="icon">
                    <img src={x} alt="x"/>
                </div>
                    <input 
                     type="text"
                     value={multiplier.str}
                     onChange={this.onMultiplierChange}
                     />
                 </div>
                   <div className="bet_btns">
                       <button
                        onClick={this.onMinMultiplier}>
                         MIN
                      </button>
                      <button
                       onClick={this.halveMultiplier}>
                         /2
                      </button>
                      <button
                       onClick={this.doubleMultiplier}>
                        X2  
                     </button>
                     <button
                       onClick={this.maxMultiplier}>
                       MAX
                    </button>
                   </div>
        </section>
    );
  }
}

export default connect(mapStateToProps)(BetboxMultiplier);


