import { combineReducers } from 'redux';
import chat from './chat/';
import multiplier from './bet/multiplierReducer';
import hash from './bet/hashReducer';
import wager from './bet/wagerReducer';
import world from './world/';

const rootReducer = combineReducers({
   hash,
   world,
   wager,
   chat,
   multiplier
});

export default rootReducer;


