import React, { Component } from 'react';
import config from '../utils/config';
import Dropdown from './Dropdown';
import BetboxBalance from './bet/BetboxBalance';
import Logo from '../struggle.svg';
import store from '../stores/';

const world = store.getState().world;

// eslint-disable //

class Navbar extends Component {
    render() {
        return (
        <section className="navbar">
            <div className="navbar_side">
                { world.user ? <BetboxBalance /> : '' }
            </div>
            <div className="navbar_center">
                <a href="/">
                     <img src={Logo} alt="struggle"/>
                </a>
            </div>
            <div className="navbar_side">
                 { world.user ? <Dropdown balance={world.user.balance} /> : <div> <a href={config.mp_browser_uri + '/login/?app_id=' + config.app_id }> LOGIN </a> or <a href={config.mp_browser_uri + '/register/'}> REGISTER </a></div> }
            </div>
        </section>
    );
  }
}

export default Navbar;


