import React, { Component } from 'react';
import store from '../../stores';
import BetboxRollButtons from './BetboxRollButtons';
import BetboxWager from '../../containers/BetboxWager';
import BetboxMultiplier from '../../containers/BetboxMultiplier';
import BetboxProfit from './BetboxProfit';
import BetboxRisk from './BetboxRisk';
import BetboxRollOutcome from './BetboxRollOutcome';
import { toggleHotkeys } from '../../actions/world';
// import Betbot from '../components/bet/betbot/Betbot';
// import Hotkeys from '../components/bet/Hotkeys';
import robot from '../../icons/icon_robot.svg';


class Betbox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showModal: false
        };
//    this.showBetbot = this.showBetbot.bind(this);
    this.toggleHotkeys = this.toggleHotkeys.bind(this);
    };

/*    showBetbot(){
        this.setState({showModal: true});
        let windowUrl =  <Betbot/>;
        let windowName = 'Betbot settings';
        let windowOpts = [
            'width=1000',
            'height=650',
            'left=20%',
            'top=20%'
        ].join(',');
        let windowRef = window.open(windowUrl, windowName, windowOpts);
        windowRef.focus();
        return false;
    }
 */   
    toggleHotkeys(){
        store.dispatch(toggleHotkeys());
    }
    render() {
        return (
            <section className="betbox .container">

                <BetboxRollOutcome />

                <BetboxWager />

                <BetboxMultiplier />

                <BetboxProfit />

                <BetboxRisk />


                <button 
                  onClick={this.showBetbot} 
                  className="betbox_toggle_bot"> 
                    AUTO BETS 
                    <img src={robot} alt="robot"/>
                 </button>
                
                <BetboxRollButtons />

            </section>
        );
    }
}

export default Betbox;



