import React, { Component } from 'react';
import store from '../stores/';
import $ from 'jquery';
import * as Actions from '../actions/chat';
import ChatboxMessages from '../components/chat/ChatboxMessages';
import ChatboxHeader from '../components/chat/ChatboxHeader';
import ChatboxUserList from '../components/chat/ChatboxUserList';
import ChatboxForm from './ChatboxForm';
import { genUuid } from '../utils/helpers';




const chat = store.getState().chat;


class Chatbox extends Component {
    constructor(props) {
        super(props);
        this.onNewMessage = this.onNewMessage.bind(this);
        this.scrollChat = this.scrollChat.bind(this);
        this.onToggleChatUserList = this.onToggleChatUserList.bind(this);
        this.onInitChat = this.onInitChat.bind(this);
    }

    onInitChat(data){
        let messages = chat.messages.map((message) => {
        message.id = this.props.genUuid();
        return messages;
    });

    chat.messages.empty();
    chat.messages.push.apply(this.props.messages, messages);

    chat.loadingInitialmessages = false;

    chat.userList = data.chat.userList;
    store.dispatch(Actions.initChat());
    }
    onNewMessage(){
        let node = this.refs.chatListRef.getDOMNode();

        let shouldScroll = function(){
            let distanceFromBottom = node.scrollHeight - ($(node).scrollTop() + $(node).innerHeight());
            console.log('DistanceFromBottom:', distanceFromBottom );
            return distanceFromBottom <= 100;
        };
        if (shouldScroll()) {
            this.scrollChat();
        }
    };
    scrollChat() {
        let node = this.refs.chatListRef.getDOMNode();
        $(node).scrollTop(node.scrollHeight);
    }

    newMessage(message){

    }
    onToggleChatUserList(){
        store.dispatch(Actions.toggleChatUserList());
    }
    render() {

        return (
            <section className="chatbox">
                <ChatboxHeader
                   onToggleChatUserList={this.onToggleChatUserList} 
                   showUserList={chat.showUserList}
                />
                
                { chat.showUserList ? <ChatboxUserList userList={chat.userList} /> : '' }

                <ChatboxMessages 
                  messages={chat.messages} 
                />
                <ChatboxForm />
            </section>
        );
    }
}

export default Chatbox;


