import React, { Component } from 'react';
import store from '../stores/';
import * as Actions from '../actions/world/';

/* eslint-disable */


const currTab = store.getState().world.currTab;

class BetTabsNav extends Component {
    constructor(props) {
        super(props);
        this.makeChangeTabHandler = this.makeChangeTabHandler.bind(this);
    };


    makeChangeTabHandler(tabName) {
       store.dispatch(Actions.changeTab(tabName));
    }

    render() {
        return (
            <ul className="bet_tabs_nav">
                <li
                    className={ currTab === 'ALL_BETS' ? 'active' : '' }>
                    <a href="javascript:void()" onClick={this.makeChangeTabHandler('ALL_BETS')} > ALL BETS  </a>
                </li>
                <li 
                   className={ currTab === 'MY_BETS' ? 'active' : '' }> 
                    <a href="javascript:void()" onClick={this.makeChangeTabHandler('MY_BETS')}> MY BETS </a>
                </li>
                <li className={ currTab === 'MY_BETS' ? 'active' : '' }>
                    <a href="javascript:void()" onClick={this.makeChangeTabHandler('MY_BETS')}> HIGH ROLLS </a>
                </li>
            </ul>
        );
    }
}


export default BetTabsNav;


