import React, { Component } from 'react';
import eye from '../../icons/icon_eye.svg';
import eye_close from '../../icons/icon_eye_close.svg'

class ChatboxHeader extends Component {
    render () {
        return (
        <section className="chatbox_header">
            <button onClick={this.props.onToggleChatUserList}> 
              { !this.props.showUserList ? <img src={eye} alt="eye"/> : <img src={eye_close} alt="eye_close"/> }
            </button>
        </section>
    );
  }
}

export default ChatboxHeader;


