import React, { Component } from 'react';
import  btc from '../../icons/icon_btc.svg';
import store from '../../stores';

const wager = store.getState().wager;
const multiplier = store.getState().multiplier;

class BetboxProfit extends Component {
    onChange(){
        this.forceUpdate();
    }
    render() {
        let profit = wager.num * (multiplier.num);
        return (
            <section className="betbox_profit">
                <span id="label"> ON WIN </span>
                <div className="input_group">
                    <div className="icon">
                    <img  src={btc} alt="btc"/>
                    </div>
                    <input
                     type="text"
                     value={profit}
                     onChange={this.onChange}
                     readOnly="readOnly"
                     />
                </div>
                <div className="bet_btns"></div>
            </section>
        );
    }
}

export default BetboxProfit;


