import React, { Component }  from 'react';
import { connect } from 'react-redux';
import * as Actions from '../actions/bet/';
import btc from '../icons/icon_btc.svg';

import store from '../stores/';

const mapStateToProps = (state) => ({
    wager: state.wager
});

const wager = store.getState().wager;



class BetboxWager extends Component {
    constructor(props) {
        super(props);
        this.onWagerChange = this.onWagerChange.bind(this);
    }
    componentWillMount(){
        this.unsubscribe = store.subscribe((onChange) => {
            this.forceUpdate();
        });
    };

    componentWillUnmount(){
        this.unsubscribe();
    }
    onWagerChange(e){
        let str = e.target.value;
        store.dispatch(Actions.updateWager({str: str}));
    }

    render() {
        return (
        <section className="betbox_wager .columns .gapless">
            <span id="label"> TOTAL BET </span>
            <div className="input_group">
                <div className="icon">
                  <img src={btc} alt="btc"/>
                </div>
                    <input 
                     type="text"
                     value={wager.str}
                     onChange={this.onWagerChange}
                     />
                </div>
                <div className="bet_btns">
                    <button> min </button>
                    <button> /2 </button>
                    <button> X2 </button>
                    <button> max </button>
                </div>
        </section>            
        );
    }
};


export default connect(mapStateToProps)(BetboxWager);


